<template>
    <div class="exchange">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>兑换红包</span>
        </header>
        <section class="s1">
            <input type="text" placeholder="请输入兑换码" v-model="exchange" v-on:input = "onInput()">
            <div class="s11">
                <input type="text" placeholder="验证码" maxlength="4" v-model="capCode" v-on:input = "onInput()">
                <div class="s12">
                    <img :src="capUrl">
                    <div class="s13">
                        <p>看不清</p>
                        <p @click="changeCode">换一张</p>
                    </div>
                </div>
            </div>
        </section>
        <div style="background-color: #ccc" v-if="isShow2">兑换</div>
        <div @click="exchangeHb" style="background-color: #4cd964" v-if="!isShow2">兑换</div>
        <section class="s2" v-if="isShow">
            <div class="s21">
                <span></span>
                <span></span>
            </div>
            <p>{{p}}</p>
            <div class="s22" @click="isShow = !isShow">确认</div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Exchange",
        data(){
            return{
                user_id:"",
                exchange:"",
                capCode:"",
                capUrl:"",
                p:"",
                isShow:false,
                isShow2:true,
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.requestCode();
        },
        methods:{
            changeCode(){
                this.requestCode();
            },
            exchangeHb(){
                this.axios.post("/v1/users/"+this.user_id+"/hongbao/exchange",{
                    exchange_code:this.exchange,
                    captcha_code:this.capCode,
                }).then(res=>{
                    // console.log(res);
                    if (res.status == 0) {
                        this.p = res.message;
                        this.isShow = !this.isShow;
                    }
                }).catch(err=>{});
            },
            onInput(){
                if (this.exchange != "" && this.capCode.length == 4){
                   this.isShow2 = false;
                }else {
                    this.isShow2 = true;
                }
            },

            //请求验证码的方法
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.capUrl = res.code;
                }).catch(err=>{});
            }
        }
    }
</script>

<style scoped>
    .exchange{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1{
        padding: 0 .5rem;
        display: block;
        margin-top: 2.65rem;
    }
    .s1>input{
        width: 94%;
        font-size: .7rem;
        color: #666;
        padding: .6rem .4rem;
        border-radius: .15rem;
        text-decoration: none;
        border: none;
        outline: none;
    }
    .s11{
        height: 2.2rem;
        display: flex;
        margin-top: .7rem;
    }
    .s11>input{
        width: 55%;
        font-size: .7rem;
        color: #666;
        padding: .6rem .4rem;
        border-radius: .15rem;
        text-decoration: none;
        border: none;
        outline: none;
    }
    .s12{
        display: flex;
        align-items: center;
        margin-left: .3rem;
        background-color: #fff;
        padding-left: .2rem;
        border-radius: .15rem;
    }
    .s12>img{
        width: 3.5rem;
        height: 1.5rem;
        margin-right: .2rem;
    }
    .s13{
        display: flex;
        flex-wrap: wrap;
        width: 2rem;
        justify-content: center;
    }
    .s13>p{
        font-size: .55rem;
        color: #666;
    }
    .s13>p:nth-child(2){
        color: #3b95e9;
        margin-top: .2rem;
    }
    .exchange>div{
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin:  .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
    }
    .s2{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .s21{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .s21>span:nth-child(1){
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .s21>span:nth-child(2){
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .s2>p{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .s22{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>